import html from '../../../utils/es6-template';
import makeObservable from '../../../utils/makeObservable';
import { Component } from '../../../public_components/component_base/component_base.js';
import { DropdownMenu } from './dropdownMenu.js';

const NAME = 'ButtonMenu';
const VERSION = '1.0';

export class ButtonMenu extends Component {
    constructor(props) {
        super(props);

        this.componentState = {
            'estadoNewButtons': this.data.text,
        };

        makeObservable(this, [           
           'updatedDataButton'
        ]); 
    }

    events () {//native component method
        if (this.data.subItems.length) {
            $(this.$el).find('.btn').on('click', (e) => {           
                if ($(this.$el).find('.subitems').is(':visible')) {
                    $(this.$el).find('.subitems').hide();
                } else {
                    $(this.$el).find('.subitems').show();
                }

                e.preventDefault();
            });
        }

        $(this.$el).find('.removeButton').on('click', (e) => {     
            this.remove();
        });

        $(this.$el).find('.updateButton').on('click', (e) => {                
            this.trigger('updatedDataButton',  this.data, $(this.$el).find('.updateText').val());
        });
    }

    removeComponent () {//native component method
        //notify in a general feedback for exmaple.
        //console.log('graph removed');
    }

    beforeRender() {//native component method
        //console.log('graph before rendered');
    }

    afterRender() {//native component method
        //console.log('graph just rendered');
    }

    updateData() {//native component method
    
    }

    updateState(state) {
        
    }

    get template() {
        const dropdownMenu = () => {
            if (this.data.subItems.length) {
                return new DropdownMenu({
                    data: {
                        'subItems': this.data.subItems
                    },
                    methods: {
                        'onClick': () => console.log('dropdownMenu clicked')
                    }
                }).render();
            } else {
                return '';
            }
        };

        return html`            
            <input type="button" class="removeButton" value="borrar" />
            <input type="button" class="updateButton" value="modificar" />
            <input type="text" class="updateText" />
            <a class="btn" href="${this.data.href}">${this.data.text}</a>
            $${dropdownMenu()}
        `;
    }
}





