import "babel-polyfill";
import { Menu } from './menu/menu.js';
require("../less/index.less");


$(() => {
    //async function foo () {
    //    const result = await fetch("https://api/?results=10", { method: "GET" }); 
    //    return result;
    //}

    //bar().then()...;

    const menu = new Menu({
        id: 'menu',
        destiny: '#root',
        data: {
            'name': 'raul',
            'surname': 'perez',
            'buttons': [{
                'text': 'boton 1',
                'href': '#',
                'subItems':  [{
                    'text': 'subboton 1',
                    'href': '#'
                },
                {
                    'text': 'subboton 2',
                    'href': '#'
                },
                {
                    'text': 'subboton 3',
                    'href': '#'
                }]
            },
            {
                'text': 'boton 2',
                'href': '#',
                'subItems': []
            },
            {
                'text': 'boton 3',
                'href': '#',
                'subItems':  [{
                    'text': 'subboton 1',
                    'href': '#'
                }]
            }]
        },
        beforeRender: () => {
            //console.log('graph before rendered');
        },
        afterRender: () => {         
        },
        updateData: (data) => {
            console.log('data has been updated!');
            console.log(data);
            //console.log(menu.componentState);          
        },
        updateState: (state) => {
            console.log(state);    
        },
        removeComponent: (data) => {            
        },        
        methods: {
            'onClick': (e, data) => {
                console.log(data);
                //console.log('click all component content');
            }
        }        
    });
     
    menu.on('newButtonAdded', (btn) => {
        console.log('nuevo botón añadido: ' + btn); 
    });
    
    menu.render();

    setTimeout(() => {
        menu.addData({
            'buttons': [{
                'text': 'boton 1',
                'href': '#',
                'subItems':  []
            },
            {
                'text': 'boton 3',
                'href': '#',
                'subItems':  [{
                    'text': 'subboton 1',
                    'href': '#'
                }]
            }]
        });


        //menu.remove();
    }, 2000);
});


